@media screen and (min-width: $desktopMin) {
	.top-section--affd {
	}

	/////////////////////
	.lob-block-mobile-tablet-unwrap,
	.lob-block-mobile-tablet-show-btn {
		display: none;
	}

	/////////////////////

	.section-profitable {
		margin-top: 86px;
	}

	.profit-block {
		margin-top: 48px;
		background: #ffffff;
		display: flex;
	}

	.profitb-controls {
		width: 436px;
		box-sizing: border-box;
		padding: 5px 40px 25px;

		background: #232323;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	.profitb-controller {
		padding: 20px 0;

		&--active,
		&:hover {
			.profitb-controller-inner {
				background: #ffe81b;
				box-shadow: -3px 0px 15px rgba(0, 0, 0, 0.25);
				padding-left: 47px;
				position: relative;
				.profitb-controller__text {
					color: #323340;
				}

				.profitb-controller__number {
					opacity: 0.4;
					color: #323340;
				}
			}
		}

		&--active {
			.profitb-controller-inner::after {
				opacity: 1;
				right: -12.5px;
			}
		}

		&:not(:last-child) {
			border-bottom: 1px solid rgba(255, 255, 255, 0.5);
		}
	}

	.profitb-controller-inner {
		box-sizing: border-box;
		padding: 27px 15px;
		margin-right: -40px;
		transition-duration: 0.2s;
		cursor: pointer;
		position: relative;

		&::after {
			opacity: 0;
			transition-duration: 0.2s;
			position: absolute;
			right: 0;
			top: 0;
			display: block;
			width: 12.5px;
			height: 100%;
			content: "";
			background: url(../img/arrow-right-horiz-tab-active.svg) no-repeat left
				center;
			background-size: auto 100%;
		}
	}

	.profitb-controller__text {
		width: 280px;
		font-weight: 500;
		font-size: 16px;
		line-height: 135%;
		color: #ffffff;
		position: relative;
	}

	.profitb-controller__number {
		font-family: $fontG;
		font-weight: 600;
		font-size: 60px;
		line-height: 125%;
		color: #dadae8;
		opacity: 0.5;
		position: absolute;
		left: -14px;
		top: -23px;
	}

	.profitb-tabs-wrap {
		width: calc(100% - 436px);
		max-width: calc(100% - 436px);
		padding: 64px 40px 47px 64px;
		height: 488px;
		box-sizing: border-box;
		overflow: hidden;
	}

	#mCSB_1_scrollbar_vertical.mCSB_scrollTools.mCSB_1_scrollbar.mCS-inset-dark.mCS-custom-theme-profitb.mCSB_scrollTools_vertical {
		.mCSB_draggerContainer {
			#mCSB_1_dragger_vertical.mCSB_dragger {
				.mCSB_dragger_bar {
					width: 9px;
					background: #dadae8;
					border-radius: 26px;
					margin: 2px 4px;
				}
			}

			.mCSB_draggerRail {
				background: #f5f6fb;
				border-radius: 26px;
				width: 13px;
			}
		}
	}

	.profitb-tabs {
	}

	.profitb-tab {
		display: none;

		&--active-desktop {
			display: block;
		}

		h3 {
			font-weight: 500;
			font-size: 16px;
			line-height: 125%;
			color: #323340;
			width: 567px;
			max-width: 100%;
			display: block;

			& + p {
				margin-top: 19px;
			}
		}

		p {
			font-size: 16px;
			line-height: 150%;
			color: #555665;
			width: 567px;
			max-width: 100%;
			display: block;

			& + p {
				margin-top: 20px;
			}

			b {
				font-weight: 500;
			}

			& + h3 {
				margin-top: 42px;
			}
		}
	}

	.profitb-tab-non-desktop-head {
		display: none;
	}

	.profitb-controller__text {
	}

	.profitb-controller__number {
	}

	.profitb-tab__inner {
	}
}

@media screen and (max-width: 1430px) and (min-width: $desktopMin) {
	.ts-flex__right {
		z-index: 0;
	}
}

@media screen and (min-width: 1300px) {
	.result-section__new_video .rs-block--affd {
		.rs-block-row__left--text {
			padding-right: 33px;
		}
	}
}

@media screen and (max-width: 1300px) {
	.video-slider-affd-wrap {
		padding: 0;
		margin: 0;
	}
}

@media screen and (min-width: $tabletMin) and (max-width: $tabletMax) {
	.wwsy-uprotch-mobile-counter--affd {
		display: none;
	}
}

@media screen and (max-width: $tabletMax) {
	.top-section--affd {
	}

	.result-section--affd {
		.rs-block {
			padding: 32px 56px 56px 56px;
		}

		.customized-ul-wrap.customized-ul-wrap--uo li:not(:last-child) {
			margin-bottom: 17px;
		}

		.rs-synchro-slider-block--uprotch {
			margin-top: 24px;
		}
	}

	.we-will-suit-you-section--diag {
	}

	.list-on-blue-section {
		padding: 73px 0 44px;
	}

	.lob-block {
		overflow: hidden;
		height: auto;
		box-sizing: border-box;
		max-height: 385px;
		margin: 48px auto 0;
		padding: 0 48px;
		transition-duration: 0.5s;
		transition-timing-function: linear;
	}

	.lob-block ul {
		column-count: 1;
	}

	.lob-block-mobile-tablet-show-btn {
		margin-top: 34px;

		&::before {
			position: absolute;
			left: 0;
			display: block;
			width: 100%;
			height: 1px;
			content: "";
			background: #828282;
		}

		span {
			cursor: pointer;

			&:hover {
				opacity: 0.8;
			}

			display: flex;
			justify-content: space-between;
			box-sizing: border-box;
			max-height: 385px;
			margin: 0 auto 0;
			padding: 0 48px;
			padding-top: 12px;
			color: #828282;
			font-size: 16px;
			line-height: 125%;

			&::after {
				display: block;
				width: 24px;
				margin-top: 4px;
				margin-right: 13px;
				height: 11px;
				content: "";
				background: url(../img/lob-block-arrow.svg) no-repeat center center;
				background-size: cover;
			}
		}
	}

	.lob-block-wrap--mobtab-visible {
		.lob-block {
			max-height: 1200px;
		}

		.lob-block-mobile-tablet-show-btn {
			display: none;
		}
	}

	////////////////////////////////

	.section-profitable {
	}

	.profit-container {
		.default-h2 {
		}
	}

	.profit-block {
		margin-top: 47px;
	}

	.profitb-controls {
		display: none;
	}

	.profitb-tabs {
	}

	.profitb-tab {
		background: #ffffff;
		border-radius: 15px;
		margin-bottom: 2px;

		&__inner {
			padding: 64px 40px 78px;
		}
	}

	.profitb-tab-non-desktop-head {
		padding: 40px 85px 36px 55px;
		background: #000;
		position: relative;

		cursor: pointer;
		transition-duration: 0.2s;

		&::after {
			content: "";
			display: block;
			background: url(../img/profitb-tabmob-arrow.svg) no-repeat center center;
			background-size: contain;

			width: 26px;
			height: 12px;
			position: absolute;
			top: 50%;
			right: 48px;
			margin-top: -6px;
			transition-duration: 0.2s;
		}

		&::before {
			content: "";
			display: block;
			width: 56px;
			height: 15px;
			background: url(../img/profirb-tabmob-triangle-active.svg) no-repeat
				center center;
			background-size: contain;
			position: absolute;
			pointer-events: none;
			bottom: -20px;
			left: 84px;
			opacity: 0;
			transition-duration: 0.2s;
		}
	}

	.profitb-controller__text {
		font-weight: 500;
		font-size: 16px;
		line-height: 135%;
		color: #ffffff;
		position: relative;
		width: 320px;
		max-width: 100%;

		span {
			position: relative;
			z-index: 1;
		}
	}

	.profitb-controller__number {
		position: absolute;
		left: -14px;
		top: -21px;
		font-weight: 600;
		font-size: 60px;
		line-height: 125%;
		color: #dadae8;
		opacity: 0.8;
		font-family: $fontG;
	}

	.profitb-tab__inner {
		display: none;
		overflow: hidden;

		h3 {
			font-weight: 500;
			font-size: 16px;
			line-height: 125%;
			color: #323340;
			width: 563px;
			max-width: 100%;

			& + p {
				margin-top: 19px;
			}
		}

		p {
			font-size: 16px;
			line-height: 150%;
			color: #555665;
			width: 563px;
			max-width: 100%;

			& + h3 {
				margin-top: 41px;
			}

			b {
				font-weight: 500;
			}
		}
	}

	.profitb-tab--active {
		.profitb-tab__inner {
		}

		.profitb-heading {
		}

		.profitb-p {
		}

		.profitb-tab-non-desktop-head {
			background: #ffe81b;

			&::after {
				transform: rotate(180deg);
			}

			&::before {
				opacity: 1;
				bottom: -15px;
			}

			.profitb-controller__text {
			}

			.profitb-controller__number {
			}
		}
	}
}

@media screen and (max-width: $phoneMax) {
	.top-section--affd {
	}

	.result-section--affd {
		.rs-block {
			padding: 0px 0 56px 0;
			margin-top: 21px;
		}
	}

	.list-on-blue-section {
		padding: 55px 0 44px;
	}

	.lob-block {
		max-height: 298px;
		margin: 37px auto 0;
		padding: 0;
	}

	.lob-block li {
		padding-left: 23px;
		font-size: 15px;
		line-height: 135%;
		margin-bottom: 19px;

		&::before {
			position: absolute;
			top: 7px;
		}
	}

	.lob-block-mobile-tablet-show-btn {
		margin-top: 22px;

		&::before {
			display: none;
		}

		span {
			&::after {
				position: absolute;
				right: 14px;
				margin: 0;
				margin-top: 5px;
				height: 11px;
				width: 17px;
			}

			padding: 0 23px 0;
		}
	}

	.how-we-work-section--affd {
		.default-h2 {
			width: 220px;
		}

		.hww-block-mobile-unwrap {
			height: 129px;
		}

		.hww-block-mobile-unwrap span {
			margin-top: 105px;
		}
	}

	.section-profitable-affd {
		margin-top: 72px;
	}

	.profit-block {
		margin-top: 35px;
	}

	.profitb-tab-non-desktop-head {
		padding: 22px 51px 19px 29px;
	}

	.profitb-controller__text {
		font-weight: normal;
		font-size: 15px;
		line-height: 135%;
	}

	.profitb-controller__number {
	}

	.profitb-tab-non-desktop-head::before {
		width: 35px;
		height: 10px;
		left: 42px;
		bottom: -15px;
	}

	.profitb-tab--active .profitb-tab-non-desktop-head::before {
		bottom: -10px;
	}

	.profitb-tab__inner {
		margin-top: -7px;
		padding: 50px 24px 40px 30px;
	}

	.profitb-tab__inner h3 {
		font-size: 15px;
		line-height: 125%;
	}

	.profitb-tab__inner p {
		font-size: 15px;
		line-height: 155%;
	}

	.profitb-tab__inner h3 + p {
		margin-top: 13px;
	}

	.profitb-tab__inner p + h3 {
		margin-top: 26px;
	}

	.profitb-tab__inner {
		border-radius: 0 0 10px 10px;
		border: 1px solid #dadae8;
		border-top: 0;
	}

	.profitb-controller__number {
		left: -13px;
		top: -15px;
	}

	.profitb-tab-non-desktop-head::after {
		width: 17px;
		height: 9px;
		top: 50%;
		right: 24px;
		margin-top: -9px;
	}
}

@media screen and (max-width: 700px) {
	.video-slider-affd-wrap .slider-white-next,
	.video-slider-affd-wrap .slider-white-prev {
		display: block !important;
	}
}

@media screen and (max-width: 450px) {
	.affd-vs-item-name {
		padding-top: 3%;
	}
}
