.our-clients-section {
	padding-top: 86px;
	margin-bottom: 86px;
}

.client_block-main + .our-clients-section {
	padding-top: 0;
}

.oc-slider-wrap {
	display: flex;
	min-width: 100%;
	margin-top: 36px;
	background: #ffffff;
}

.oc-slider {
	width: calc(100% - 81px);

	&.slick-vertical .slick-slide {
		border: 0;
	}
}

.oc-slide-inner {
	display: flex;
	width: 100%;
	height: 100%;
}

.oc-slide-pic {
	position: relative;
	width: 400px;
	min-width: 400px;

	&::before {
		display: block;
		padding-top: 81.25%;
		content: "";
	}

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center center;
		filter: grayscale(100%);
		filter: gray;
	}
}

.oc-slide-content {
	padding: 48px 78px 48px 98px;
}

.oc-slide-quote {
	position: relative;
	padding-top: 25px;
	color: #232323;
	font-size: 16px;
	line-height: 150%;

	&::before {
		position: absolute;
		top: 25px;
		left: -58px;
		display: block;
		width: 34px;
		height: 23px;
		content: "";
		background: url(../img/quote.svg) no-repeat center center;
		background-size: contain;
	}

	a {
		text-decoration: underline;
		cursor: pointer;
		transition-duration: 0.2s;

		&:hover {
			text-decoration: none;
		}
	}
}

.oc-slide-row {
	display: flex;
	align-items: center;
	margin-top: 61px;
}

.oc-slide-row-logo {
	display: flex;
	align-items: center;
	height: 87px;

	&::after {
		display: block;
		width: 1px;
		height: 47px;
		margin: 0 24px;
		content: "";
		background: #dfdfdf;
	}

	img {
		width: auto;
		max-height: 87px;
	}
}

.oc-slide-row-info {
	color: #232323;
	font-size: 16px;
	line-height: 150%;

	&__fio {
		font-weight: 500;
	}

	&__position {
		margin-top: -2px;
		color: #5c5c5c;
	}
}

.oc-slider-controls {
	position: relative;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: space-between;
	box-sizing: border-box;
	width: 81px;
	margin: 48px 0;
	border-left: 1px solid #dfdfdf;

	.slick-dots {
		display: flex;
		align-items: center;
		flex-direction: column;
		flex-grow: 1;
		justify-content: center;
		order: 1;
		list-style: none;

		li {
			position: relative;
			display: block;
			width: 7px;
			height: 7px;
			margin: 12px 0;
			cursor: pointer;
			border-radius: 50%;
			background: #c7c7d5;

			button {
				display: none;
			}

			&::before {
				position: absolute;
				z-index: 1;
				top: -1px;
				left: -1px;
				display: block;
				width: 9px;
				height: 9px;
				content: "";
				transition-duration: 0.2s;
				opacity: 0;
				border-radius: 50%;
				background: #ffe81b;
			}

			&:hover {
				&::before {
					opacity: 0.5;
				}
			}

			&.slick-active {
				&::before {
					opacity: 1;
				}
			}
		}
	}
}

.oc-slick-prev.slick-arrow,
.oc-slick-next.slick-arrow {
	width: 18px;
	margin: 0;
	cursor: pointer;

	svg {
		display: block;
		height: 9px;
		margin: 0;

		path {
			transition-duration: 0.1s;
		}
	}

	&:hover {
		svg {
			path {
				stroke: #ffb800;
			}
		}
	}
}

.oc-slick-prev {
	order: 0;
}

.oc-slick-next {
	order: 2;
}

//------------ОТВЕТЫ НА ВОПРОСЫ---------------------------------------------

.answers-section {
	padding-bottom: 112px;

	&--margin {
		padding-top: 88px;

		@media screen and (max-width: $tabletMax) {
			padding-top: 0;
		}
	}
}

.ans-block {
	box-sizing: border-box;
	margin-top: 36px;
	border: 1px solid #dfdfdf;
}

.ans-item {
	&:not(:last-child) {
		border-bottom: 1px solid #dfdfdf;
	}
}

.ans-item-question {
	position: relative;
	display: flex;
	align-items: flex-start;
	padding: 24px 85px 23px 32px;
	cursor: pointer;
	transition-duration: 0.2s;

	&--opened {
		.ans-iq-li-svg-vert {
			opacity: 0;
		}

		.ans-iq-li-svg-main {
			stroke: #555665;
		}

		.ans-iq-li-svg-horiz,
		.ans-iq-li-svg-vert {
			fill: #555665;
		}

		.ans-iq-right-indicator {
			transform: rotate(180deg);
		}
	}

	&:hover {
		background-color: #ffe81b;

		.ans-iq-li-svg-main {
			stroke: #232323;
		}

		.ans-iq-li-svg-horiz,
		.ans-iq-li-svg-vert {
			fill: #232323;
		}

		.ans-iq__text {
			color: #232323;
		}
	}
}

.ans-iq-left-indicator {
	margin-right: 20px;

	svg {
		display: block;

		* {
			transition-duration: 0.2s;
		}
	}
}

.ans-iq__text {
	width: 100%;
	max-width: calc(710px - 40px);
	transition-duration: 0.2s;
	font-weight: 500;
	font-size: 16px;
	line-height: 135%;
}

.ans-iq-right-indicator {
	position: absolute;
	top: 31px;
	right: 54px;
	width: 18px;
	height: 9px;
	transition-duration: 0.2s;
	background: url(../img/answers.svg) no-repeat center center;
	background-size: cover;
}

.ans-item-answer {
	position: relative;
	display: none;
	box-sizing: border-box;
	padding: 25px 32% 22px 96px;
	background: #ffffff;
	font-size: 16px;
	line-height: 135%;

	&::before {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 11px;
		height: 100%;
		content: "";
		background: #f3db00;
	}

	ul {
		margin-left: 18px;
	}
}

@media screen and (min-width: 1300px) {
	.fancybox-slide--image {
		overflow: scroll;
		overflow-x: hidden;
	}

	body.fancybox-active {
		overflow: hidden;
	}
}

.dbr,
.tbr,
.mbr {
	display: none;
}

@media screen and (min-width: $desktopMax) {
	.dbr {
		display: inline;
	}
}

@media screen and (min-width: $tabletMin) and (max-width: $tabletMax) {
	.tbr {
		display: inline;
	}
}

@media screen and (max-width: $phoneMax) {
	.mbr {
		display: inline;
	}
}

